import * as actionTypes from './actionTypes';
import axios from '../../axios-orders';

//moving the action creators from the BurgerBuilder.js to here

export const addIngredient = (name) => {
    return {
        type: actionTypes.ADD_INGREDIENT,
        ingredientName: name
    }
};

export const removeIngredient = (name) => {
    return {
        type: actionTypes.REMOVE_INGREDIENT,
        ingredientName: name
    }
};

export const setIngredients = (ingredients) => {
    return {
        type: actionTypes.SET_INGREDIENTS,
        ingredients: ingredients
    }
};

export const fetchIngredientsFailed = () => {
    return {
        type: actionTypes.FETCH_INGREDIENTS_FAILED
    }
}

export const initIngredients = () => {
    return dispatch => {
        axios.get('https://react-my-burger-89734.firebaseio.com/ingredients.json') //removed as using redux now
        .then(response => {
            dispatch(setIngredients(response.data));
        })
        .catch(error => {
            dispatch(setIngredients(fetchIngredientsFailed()));
        });
    };
}


